/**
 * 
 * @param  {[String]}  tab  []
 * @param  {[Boolean]} top  []
 * @param  {[Boolean]} good []
 * @return {[String]}       [label]
 */
export const getLabel = (tab, top, good) => {
	let str = "";

	if (top) {
		str = "";
	} else if (good) {
		str = "";
	} else {
		switch (tab) {
			case "ask":
				str = "";
				break;
			case "share":
				str = "";
				break;
			case "job":
				str = "";
				break;
			case "good":
				str = "";
				break;
			default:
				str = "";
		}
	}
	return str;
}

/**
 * ISOGMT   
 * @param {string} timeString ISOGMT
 * @return {string}     
 */
export const getTimeFromNow = (timeString) => {
	// 
	const a = (new Date(timeString)).getTime();
	const b = Date.now();
	const ms = b - a;
	// 
	let second = Math.floor(ms / 1000);
	let year = Math.floor(second / (60 * 60 * 24 * 365)); //
	let month = Math.floor((second - year * (60 * 60 * 24 * 365)) / (60 * 60 * 24 * 30)); //
	let day = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30)) / (60 * 60 * 24)); //
	let hour = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30) - day * (60 * 60 * 24)) / (60 * 60)); //
	let minute = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30) - day * (60 * 60 * 24) - hour * (60 * 60)) / 60); //
	let remainSecond = Math.floor((second - year * (60 * 60 * 24 * 365) - month * (60 * 60 * 24 * 30) - day * (60 * 60 * 24) - hour * (60 * 60) - minute * 60)); //

	if (year > 0) {
		return `${year}`;
	} else if (month > 0) {
		return `${month}`;
	} else if (day > 0) {
		return `${day}`;
	} else if (hour > 0) {
		return `${hour}`;
	} else if (minute > 0) {
		return `${minute}`;
	}  else if (remainSecond > 0) {
		return `${remainSecond}`;
	} else {
		return `0`;
	}
}


